<template>
    <div class="bgColor">
        <div class="header">
            <img src="../../../images/userAgreement.png" alt="">
        </div>
        <div class="curatorText">
            <span>欢迎您使用三六九游戏客户端软件服务！</span><br/>
            <span>
                为使用三六九游戏客户端软件（以下简称“本软件”）及服务，您应当阅读并遵守《三六九游戏客户端软件许可证及服务协议》（以下简称“本协议”），以及《三六九服务协议》。请您务必审慎阅读、充分理解各条款内容，特别是免除或者限制责任的条款，以及开通或者使用某项服务的单独协议，并选择接受或者不接收；限制、免责条款可能以加粗形式提醒您注意。
            </span><br/>
            <span>
                除非您已阅读并接受本协议所有条款，否则您无权下载、安装、使用获取三六九账号、登录等行为即视为您已阅读并同意上述协议约束。
            </span><br/>
            <span>如果您未满18周岁，请在法定监护人的陪同下阅读本协议及其他上述协议，并特别注意未成年使用条款。</span>
        </div>
        <footer-text></footer-text>

    </div>

</template>

<script>
import footerText from '../../../components/footerText'
export default {
    components: {
        footerText,
  },
  data() {
      return {
      }
  },
  methods:{

  }
}
</script>

<style lang="less" scoped>
    .bgColor{
        height:100%;
        background:#EEEEEE;
        position: relative;
        #footer_text{
            position: absolute;
            bottom:0;
        }
    }
    .header{
        height: 26%;
        background: #4CACFF;
        text-align: center;
        position: relative;
        img{
            width:84%;
            margin-top:14%;
        }
    }
    .curatorText{
        width:84%;
        height: 45%;
        overflow-y:auto;
        position: absolute;
        top:20%;
        left:8%;
        color:#F9B97C;
        font-weight: 400;
        border-radius: 10px;
        background:#fff;
        text-align: center;
        font-family:"Microsoft YaHei";
        span{
            width:96%;
            text-align: left;
            display: inline-block;
            text-indent: 1.8em;
            // margin-left:1.8em;
            font-size:.9em;

        }
    }

</style>



